html,
body {
	height: 100%;
}

body {
	background: radial-gradient(ellipse at center, #fffeea 0%, #fffeea 35%, #b7e8eb 100%);
	overflow: hidden;
}

.shipping-wrapper {
	top: 0px;
	background: #fff;
	width: 550px;
	height: 550px;
	border-radius: 50%;
	overflow: hidden;
	position: relative;
	box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.05);
	margin: 100px auto;
}

.ocean {
	height: 5%;
	width: 100%;
	position: absolute;
	bottom: 0;
	left: 0;
	background: #015871;
}

.wave {
	background: url(https://cdn.kcak11.com/codepen_assets/wave_animation/wave.svg) repeat-x;
	position: absolute;
	top: -198px;
	width: 6400px;
	height: 198px;
	animation: wave 7s cubic-bezier(0.36, 0.45, 0.63, 0.53) infinite;
	transform: translate3d(0, 0, 0);
}

.pirate {
	width: 512px !important;
	position: absolute;
	top: 12px;
	margin-left: auto;
	animation: shipmove 2s cubic-bezier(0.36, 0.45, 0.63, 0.53) infinite alternate;
}

.wave:nth-of-type(2) {
	top: -175px;
	animation: wave 7s cubic-bezier(0.36, 0.45, 0.63, 0.53) -3.5s infinite, swell 7s ease -1.25s infinite;
	opacity: 1;
}

@keyframes wave {
	0% {
		margin-left: 0;
	}
	100% {
		margin-left: -1600px;
	}
}

@keyframes swell {
	0%,
	100% {
		transform: translate3d(0, -25px, 0);
	}
	50% {
		transform: translate3d(0, 5px, 0);
	}
}

@keyframes shipmove {
	0% {
		transform: translate3d(0, 0px, 0);
	}
	100% {
		transform: translate3d(0, 40px, 0);
	}
}
.endWave {
	display: none;
}
