.payment-animation-wrapper {
	width: 600px;
	height: 280px;
	background: transparent;
	position: absolute;
	overflow: hidden;
	left: 0px;
	animation: camera 20s ease-in-out infinite;
}
@keyframes camera {
	0% {
		left: 0;
	}
	50% {
		left: -260px;
	}
	100% {
		left: 0;
	}
}
body {
	background: #eee;
	overflow-y: hidden;
}
.bill {
	width: 50px;
	height: 28px;
	background: #00d56a;
	border-radius: 3px;
	color: #00b75e;
	font-family: Arial;
	text-align: center;
	line-height: 28px;
	font-size: 14px;
	position: absolute;
	top: 137px;
	left: -60px;
	animation: billPath 3s ease-in-out infinite;
}
.bill:after {
	content: '';
	position: absolute;
	top: 2px;
	left: 2px;
	right: 2px;
	bottom: 2px;
	background: transparent;
	border-radius: 2px;
	border: 1px solid #05c769;
}
.bill-2 {
	animation-delay: 1s;
}
.bill-3 {
	animation-delay: 2s;
}
.bill-4 {
	animation-delay: 1.5s;
}
.bill-5 {
	animation-delay: 2.5s;
}
.bill-6 {
	animation-delay: 3.5s;
}
@keyframes billPath {
	from {
		left: -60px;
	}
	to {
		left: 400px;
	}
}
.huegel {
	width: 1000px;
	height: 180px;
	background: #7dd390;
	position: absolute;
	border-radius: 50%;
	bottom: -120px;
	right: -350px;
}
.wall-1-container {
	position: absolute;
	-webkit-perspective: 93px;
	perspective: 93px;
	top: 10px;
	left: 0px;
}
.wall-1 {
	width: 150px;
	height: 150px;
	background: #ddd;
	-webkit-transform: rotateX(0deg) rotateY(-5deg);
	transform: rotateX(0deg) rotateY(-5deg);
	position: relative;
}
.schild {
	position: absolute;
	background: #fff65c;
	top: 25px;
	left: 33px;
	right: 27px;
	bottom: 35px;
	border-radius: 50%;
	text-align: center;
	line-height: 90px;
	vertical-align: center;
	font-size: 30px;
	font-family: Arial;
}
.wall-2-container {
	position: absolute;
	-webkit-perspective: 79px;
	perspective: 79px;
	top: 23px;
	left: 154px;
}
.wall-2 {
	width: 50px;
	height: 149px;
	background: #ccc;
	-webkit-transform: rotateX(0deg) rotateY(40deg);
	transform: rotateX(0deg) rotateY(40deg);
}
.geldspeicher {
	width: 240px;
	height: 180px;
	left: 310px;
	top: 68px;
	position: absolute;
	animation: scaleGeldspeicher 3s ease-in-out infinite;
	animation-delay: 2.5s;
}
@keyframes scaleGeldspeicher {
	0% {
		transform: scale(1);
	}
	10% {
		transform: scale(1.035);
	}
	20% {
		transform: scale(1);
	}
	100% {
		transform: scale(1);
	}
}
.payment-animation {
	top: 100px;
	background: #fff;
	width: 280px;
	height: 280px;
	border-radius: 50%;
	overflow: hidden;
	position: relative;
	box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.05);
	margin: 100px auto;
}
